html {
  box-sizing: border-box;
  background-color: black;
}

*, *::before, *::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

.app {
  width: 100%;
  margin: 0 auto;
}


h1 {
  margin-bottom: 20px;
}

p {
  margin-bottom: 15px;
}

a:-webkit-any-link{
  color:black;
  text-decoration-line: none !important;
}

/*START NAVIGATION*/
nav ul {
  z-index: 9999;
  list-style: none;
  color: black;
  position: fixed;
  display: flex;
  margin-bottom: 20px;
}
nav ul li {
  padding: 20px;
}
nav ul li{
  color: white;
  text-decoration: none;
}
.current {
  border-bottom: 4px solid white;
}
button {
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
  outline: 0;
  border: 1px solid white;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  margin: 5px 10px 5px 10px;
  text-decoration: none;
  padding: 5px;
}
button::after {
  content: "";
  background-color: #ffe54c;
  width: 100%;
  z-index: -1;
  position: absolute;
  height: 100%;
  top: 5px;
  left: 5px;
  transition: 0.2s;
  text-decoration: none;
}
button:hover::after {
  top: 0px;
  left: 0px;
  text-decoration: none;
}
/*END NAVIGATION*/

/* START INTRO_TEXT */
.box-intro_txt{
  width: 100%;
  height: auto;
  position: relative;
  color:white;
  padding:50px 50px;
  background: linear-gradient(270deg,rgba(103, 161, 147, 0.35), rgba(218, 166, 109, 0.35), rgba(114, 109, 218, 0.35), rgba(191, 109, 218, 0.35));
    background-size: 800% 800%;
    -webkit-animation: AnimationName 10s ease infinite;
    -moz-animation: AnimationName 10s ease infinite;
    animation: AnimationName 10s ease infinite;
}
@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
#title-intro_txt {
  font-size: 2rem;
  text-transform: uppercase;
}
#descr-intro_txt {
  font-size: 0.95rem;
  margin-top: 20px;
  margin-bottom: 20px;
  column-count: 2;
  column-gap: 50px;
}
/* END INTRO_TEXT */